
	#searchBg {
		display: none;
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		background-color: black;
		z-index: 1001;
		-moz-opacity: 0.7;
		opacity: .70;
		filter: alpha(opacity=70);
	}
	#searchDiv {
		display: none;
		position: absolute;	
		
		width: 75%;
		height: 70px;
		background-color: white;
		z-index: 1002;
		border-radius:5px;
		-moz-border-radius:5px; 
		min-width: 300px;
		max-width: 700px;
	}
	@media(max-width:669px){
		#searchDiv {
			top: 200px;
			left: 15%;
		}
	}
	@media(min-width:669px){
		#searchDiv {
			top: 300px;
			left: 30%;
		}
	}
	#searchContent{
		height: 100%;
		width: 78%;
		
		text-align:center;
		border-radius:5px 0 0 5px;
		border-width: 0;
		padding: 0;
		-moz-border-radius:5px 0 0 5px;
		margin: 0 auto;
		font-size: 35px;
		float: left;
	}
	#searchBtn{
		height: 100%;
		width:auto;
		background-color: black;
		text-align:center;
		border-radius:0 5px 5px 0;
		-moz-border-radius:0 5px 5px 0;
		font-size: 45px;
		margin: 0 auto;
	}
	/* basic style */
	.close {
		/* still bad on picking color */
		background: black;
		color: white;
		/* make a round button */
		border-radius: 14px;
		/* center text */
		line-height: 16px;
		text-align: center;
		height: 24px;
		width: 24px;
		font-size: 16px;
	}
	/* use cross as close button */
	.close::before {
		content: "\2716";
	}
	/* place the button on top-right */
	.close {
		top: -8px;
		right: -10px;
		position: absolute;
	}
